@import url('https://fonts.googleapis.com/css2?family=Assistant:wght@500&display=swap');

:root {

  --mainOpacity: rgba(0, 0, 0, 0.145);
  --blackOpacity: rgba(0, 0, 0, 0.849);
  --whiteOpacity: rgba(255, 255, 255, 0.659);
  --whiteLabel: rgb(255, 255, 255);
  --silverLabel: rgb(239, 239, 239);
  --matteBlack: rgb(39, 39, 39);
  --purpleLabel: rgb(119, 83, 162); 
  --blackLabel: rgb(10, 10, 10);
  --greyLabel: rgb(162, 162, 162);
  --yellowLabel: rgb(231, 189, 35); 

}

/* Animations */

/* Create a new keyframe animation called "fadeInScrollUp" */
@keyframes fadeInScrollUp {
  0% {
    opacity: 0;
    transform: translateY(50px);
    /* Initial position: 50px down from its original position */
  }

  100% {
    opacity: 1;
    transform: translateY(0);
    /* Final position: Original position (scroll up) */
  }
}

.fade-in {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 1s, transform 1s;
}

.fade-in.fade-in-visible {
  opacity: 1;
  transform: translateY(0);
}


.container {

  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 30px;
  padding-top: 5%;
  margin: 5%;
  animation: fadeInScrollUp 1s ease-in-out;

}

.container-2 {

  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 10px;
  padding: 5%;
  margin: 0;
  animation: fadeInScrollUp 1s ease-in-out;

}

.left-text {

  font-size: 85px;
  text-align: left;

}

.description-left {

  text-align: left;
  font-size: 24px;

}

.mid-image {

  background-image: url('img/parris_gainer.jpg');
  background-size: cover;
  background-position: center;
  background-color: var(--blackOpacity);
  padding: 30px;
  box-shadow: 10px 25px 25px 10px var(--mainOpacity);
  border-radius: 15px;

}

.App {

  text-align: left;
  overflow-x: hidden;
  margin: 0;
  align-items: center;
  justify-content: center;

}

.Header {

  background-color: var(--silverLabel);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: center;
  font-size: calc(60px + 2vmin);
  font-family: 'Assistant', sans-serif;
  font-weight: bold;
  color: var(--purpleLabel);

}

.img-banner {
  background-image: url('img/parrisgainer_az.jpg');
  background-size: auto;
  background-color: var(--blackOpacity);
  padding: 0;
  box-shadow: 10px 25px 25px 10px var(--mainOpacity);
  border-radius: 15px;
  width: 308px;
  height: 687px;

}

.description-mid {

  text-align: left;
  justify-content: center;
  color: var(--greyLabel);
  font-size: 35px;
  margin: 5%;
  animation: fadeInScrollUp 1s ease-in-out;

}

/* Navigation Menu */

nav {

  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  background-color: var(--whiteLabel);
  box-shadow: 0.5px 0.5px 0.5px 0.5px var(--mainOpacity);
  color: var(--blackLabel);
  padding: 1%;
  padding-left: 1%;
  padding-bottom: 1%;
  position: fixed;
  font-size: 1em;
  top: 0;
  left: 0;
  width: 100%;
  margin-top: -4px;
  z-index: 1;
  order: 2;

}

.logo {

  padding: 0;
  margin: 0;
}


.menu {

  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  justify-content: center;
  opacity: 0s;

}

.menu li {

  margin: 0 20px;
  margin-left: 20px;

}

.menu a {

  color: var(--purpleLabel);
  text-decoration: none;
  padding: 20px;
  transition: all 0.5s;


}

.menu a:hover {

  color: var(--yellowLabel);
  text-decoration: none;
  padding: 20px;
  background-color: var(--purpleLabel);


}

.menu.open {

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-color: var(--matteBlack);
  box-shadow: 5px 5px 5px var(--mainOpacity);
  padding: 30%;
  position: absolute;
  top: 100%;
  left: 0;

}

.menu.open li {

  opacity: 1;
  margin: 5%;
  border-radius: 10px;
  transform: translateY(0);
  transition: opacity 0.3s, transform 0s linear 0s;

}

.menu.open li:nth-child(1) {

  transition-delay: 0.35s;

}

.menu.open li:nth-child(2) {

  transition-delay: 0.45s;

}

.menu.open li:nth-child(3) {

  transition-delay: 0.55s;

}

.menu.open li:nth-child(4) {

  transition-delay: 0.65s;

}

/* Overlay styles 
NOTE: This is used when the hamburger menu opens and the rest of the viewport is overlayed with a blur effect in the background. 
*/
.overlay {

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--mainOpacity);
  z-index: 1;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease;
  backdrop-filter: blur(10px);

}

.overlay.open {

  opacity: 1;
  visibility: visible;

}


/* Hamburger Menu - Main */

.hamburger {

  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: transform 0.3s;
  margin-left: 10px;
  /* Adjust the left margin as needed */
  padding: 25px;

}

.hamburger span {

  background-color: var(--blackLabel);
  box-shadow: 1px 1px 1px var(--mainOpacity);
  display: block;
  height: 3px;
  margin: 3px 0;
  width: 28px;
  transition: transform 0.8s, opacity 1s;

}


.hamburger.active span:nth-child(1) {

  transform: rotate(45deg) translate(7px, 5px);

}

.hamburger.active span:nth-child(2) {

  opacity: 0;

}

.hamburger.active span:nth-child(3) {

  transform: rotate(-45deg) translate(7px, -5px);

}

/* Your existing styles here */

.footer {
  background-color: var(--blackLabel);
  color: var(--whiteLabel);
  padding: 20px;
  text-align: center;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
}

.footer p {
  font-size: 14px;
}



/* Define the media query */

@media (min-width: 1024px) {

  .container {

    grid-template-columns: repeat(3, 1fr);
    margin: 5%;

  }

  .left-text {

    font-size: 110px;

  }

  .mid-image {

    padding: 50%;

  }

  .description-left {

    font-size: 24px;

  }
}

@media (max-width: 1328px) {

  .container {

    grid-template-columns: repeat(1, 1fr);
    padding-top: 10%;
    margin: 5%;

  }

  .left-text {

    font-size: 80px;
  }

  .mid-image {

    padding: 50%;

  }

  .description-left {

    font-size: 24px;

  }

  /* Hamburger Menu */

  .hamburger {
    display: flex;
    /* Show the hamburger menu on smaller screens */
  }

  /* Navbar */

  .menu {
    width: 100%;
    padding-top: 40%;
    display: none;
    animation: fade-in 0.5s ease-in-out forwards;
    /* Hide menu by default */
  }

  .menu.open {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background-color: var(--whiteLabel);
    padding: 30px;
    position: absolute;
    top: 100%;
    left: 0;
    opacity: 1;
    visibility: visible;
    transition: opacity 0.3s, visibility 0s linear 0s;
  }

  .menu li {

    margin: 10px 0;

  }

  .menu a:hover {

    width: 100%;

  }

}